import React, { PropTypes } from 'react'
import { Link } from 'react-router'

const ListLinkItem = ({ item }) => {
    const { name, oid } = item
    return (
        <div className="mb2 mr2 inline-block">
            <Link className="text-decoration-none py2 btn btn-primary" to={`/wlm/objects/${oid}`}>
                <div className="white">
                    {name}, oid: {oid}
                </div>
            </Link>
        </div>
    )
}

ListLinkItem.propTypes = {
    item: PropTypes.shape({
        name: PropTypes.string.isRequired,
        oid: PropTypes.any.isRequired
    })
}

export default ListLinkItem


