import * as ActionTypes from '../actions'

// auth reducer
function auth(state = {
    isFetching: false,
    isAuthenticated: sessionStorage.getItem('capi_token') ? true : false,
    userType: sessionStorage.getItem('capi_user'),
    failedRequests: 0,
  }, action) {
  switch (action.type) {
    case ActionTypes.CAPI_LOGIN_REQUEST:
      return Object.assign({}, state, {
        isFetching: true,
        isAuthenticated: false,
        errorMessage: '',
        user: action.creds
      })
    case ActionTypes.CAPI_LOGIN_SUCCESS:
      return Object.assign({}, state, {
        isFetching: false,
        isAuthenticated: true,
        errorMessage: '',
        userType: action.userType
      })
    case ActionTypes.CAPI_LOGIN_FAILURE:
      return Object.assign({}, state, {
        isFetching: false,
        isAuthenticated: false,
        errorMessage: action.message
      })
    case ActionTypes.CAPI_LOGOUT_SUCCESS:
      return Object.assign({}, state, {
        isFetching: false,
        isAuthenticated: false
      })
    default:
      return state
  }
}

export default auth


