import React, { Component, PropTypes } from 'react'
import { connect } from 'react-redux'
import { loadVersion } from '../actions'

const loadData = ({ loadVersion }) => {
  loadVersion()
}

class Version extends Component {
  static propTypes = {
    isFetching: PropTypes.bool.isRequired,
    engController: PropTypes.string,
    engData: PropTypes.object.isRequired
  }

  componentDidMount() {
    loadData(this.props)
  }

  handleReload = () => {
    return loadData(this.props)
  }

  render() {
    const { isFetching, engData } = this.props
    const buttonClass = (isFetching === true ? "progress" : "btn btn-outline")

    let fwVerStr, buildDate, buildTime

    if (engData['undefined']) {
      fwVerStr = engData['undefined'].firmwareVersion
      buildDate = engData['undefined'].buildDate
      buildTime = engData['undefined'].buildTime
    }

    return (
      <div className="mb2">
        <div className="py3 px3 clearfix bg-etn-blue">
          <div className="max-width-3 mx-auto">
            <div className="mt3 mb2 h2 white">WaveLinx System</div>
            <hr />
            <div className="white py1">
              <div className="left">
                <div className="mb1">FW ver: {fwVerStr}</div>
              </div>
              <div onClick={this.handleReload} className={`${buttonClass} mt1 right`}>
                {isFetching ? '' : 'Refresh'}
              </div>
            </div>
          </div>
        </div>
        <div className="clearfix">
          <div className="max-width-3 mx-auto px2">
            <h2 className="h3 mt3 mb2" key={'linkedObj'}>Gateway Info</h2>
            <div className="">
              <div className="mb1">Firmware: {fwVerStr}</div>
              <div className="mb1">Build Date: {buildDate}</div>
              <div className="mb1">Build Time: {buildTime}</div>
            </div>
          </div>
        </div>
      </div>
    )
  }
}

const mapStateToProps = state => {
  const { system } = state
  const { isFetching, engData } = system
  return {
    isFetching,
    engData
  }
}

export default connect(mapStateToProps, {
  loadVersion,
})(Version)



