import React, { Component, PropTypes } from 'react'
import { connect } from 'react-redux'
import { loadEntity } from '../../actions'
import OccSetOverview from '../../components/overviews/OccSetOverview'
import ZoneOverview from '../../components/overviews/ZoneOverview'

const loadData = ({ loadEntity, oid }) => {
  loadEntity(oid)
}

class Area extends Component {
  static propTypes = {
      entities: PropTypes.object.isRequired,
      result: PropTypes.number.isRequired,
      isFetching: PropTypes.bool.isRequired,
      oid: PropTypes.number.isRequired,
      loadEntity: PropTypes.func.isRequired
  }

  componentWillReceiveProps(nextProps) {
    if (nextProps.oid !== this.props.oid) {
      loadData(nextProps)
    }
  }

  componentDidMount() {
    loadData(this.props)
  }

  onLoadDetailsClick = () => {
    loadData(this.props)
  }

  occSetArray(obj) {
    let newArray = []
    for (let thisKey in obj) {
      if({}.hasOwnProperty.call(obj,thisKey)) {
        newArray.push(<OccSetOverview oid={thisKey} data={obj[thisKey]} key={thisKey}/>)
      }
    }
    return newArray
  }

  zoneArray(obj) {
    let newArray = []
    for (let thisKey in obj) {
      if({}.hasOwnProperty.call(obj,thisKey)) {
        newArray.push(<ZoneOverview oid={thisKey} data={obj[thisKey]} key={thisKey}/>)
      }
    }
    return newArray
  }

  render() {
    const { entities, result, isFetching, oid } = this.props

    const buttonClass = (isFetching === true ? "progress mt2" : "btn btn-outline")

    let objectType, name, isOccupiedPhysical, isOccupiedLogical,
        selectedScene, showOccSets, showZones

    if (entities && entities.objects) {
        objectType = entities.objects[oid].Name
        name = entities.objects[oid].name
        selectedScene = entities.objects[oid].selectedScene
        isOccupiedPhysical = (entities.objects[oid].isOccupiedPhysical === true ? "TRUE" : "FALSE")
        isOccupiedLogical = (entities.objects[oid].isOccupiedLogical === true ? "TRUE" : "FALSE")
    }

    if (result && entities && entities.objects[oid].occupancySets) {
        const occSetOids = entities.objects[oid].occupancySets
        let occSetObjects= {}

        for (let i = 0; i < occSetOids.length; i++) {
            occSetObjects[occSetOids[i]] = entities.objects[occSetOids[i]]
        }

        showOccSets = this.occSetArray(occSetObjects)
    }

    if (result && entities && entities.objects[oid].zones) {
        const zoneOids = entities.objects[oid].zones
        let zoneObjects= {}

        for (let i = 0; i < zoneOids.length; i++) {
            zoneObjects[zoneOids[i]] = entities.objects[zoneOids[i]]
        }

        showZones = this.zoneArray(zoneObjects)
    }

    return (
      <div className="mb2">
        <div className="py3 px3 clearfix bg-etn-blue">
          <div className="max-width-3 mx-auto">
            <div className="mt3 mb1 h4 white">{objectType}</div>
            <div className="mb2 h2 white">{name}</div>
            <hr />
            <div className="white py1">
              <div className="left">
                <div className="mb1">Selected Scene: {selectedScene}</div>
                <div className="mb1">Occupied Physical: {isOccupiedPhysical}</div>
                <div className="mb1">Occupied Logical: {isOccupiedLogical}</div>
              </div>
              <div onClick={this.onLoadDetailsClick} className={`${buttonClass} mt1 right`}>
                {isFetching ? '' : 'Refresh'}
              </div>
            </div>
          </div>
        </div>
        {showZones &&
          <div className="clearfix py3 bg-lt-gray">
            <div className="max-width-4 mx-auto">
              <div className="h2 mb2 ml1">Zones</div>
              <div className="mxn2">
                {showZones}
              </div>
            </div>
          </div>
        }
        {showOccSets &&
          <div className="clearfix py3 bg-lt-gray">
            <div className="max-width-4 mx-auto">
              <div className="h2 mb2 ml1">Occupancy Sets</div>
              <div className="mxn2">
                {showOccSets}
              </div>
            </div>
          </div>
        }
      </div>
    )
  }
}

const mapStateToProps = (state, ownProps) => {
    const oid = ownProps.params.oid

    const { lightEngine } = state
    const { info, data } = lightEngine
    const { isFetching } = info
    const { entities, result} = data

    return {
        entities,
        result,
        isFetching,
        oid
    }
}

export default connect(mapStateToProps, {
  loadEntity,
})(Area)



