import React, { PropTypes } from 'react'
import { Link } from 'react-router'

const AreaOverview = ({ oid, data }) => {
    const { areaStatus, id, Name, name, isOccupiedPhysical, isOccupiedLogical, isReachable } = data

    const occStatusPhys = (isOccupiedPhysical === true ? "TRUE" : "FALSE" )
    const occStatusLog = (isOccupiedLogical === true ? "TRUE" : "FALSE" )
    const reachableStatus = (isReachable === true ? "TRUE" : "FALSE" )
    const statusOutline = (isOccupiedLogical === true ? "grn-shadow" : "rd-shadow")

    let statusBg, statusText

    switch(areaStatus) {
        case 'GOOD':
            statusBg = "bg-white"
            statusText = "black"
            break
        default:
            statusBg = "bg-etn-red-light"
            statusText = "white"
            break
    }

    return (
        <div className="center xs-col xs-col-6 md-col-4 lg-col-3 px1 py1">
            <Link className={`${statusOutline} ${statusBg} overview-box hover-box left-align py3 btn btn-primary`} to={`/wlm/engine/area/${oid}`}>
                <div className={`${statusText}`}>
                    <div className="h3 mb1">{name}</div>
                    <p className="mb2">{Name}</p>
                    <p>ID: {id}</p>
                    <p>Occ Phys: <b>{occStatusPhys}</b></p>
                    <p>Occ Log: <b>{occStatusLog}</b></p>
                    <p>Reachable? <b>{reachableStatus}</b></p>
                </div>
            </Link>
        </div>
    )
}

AreaOverview.propTypes = {
    oid: PropTypes.string.isRequired,
    data: PropTypes.shape({
        areaStatus: PropTypes.string,
        id: PropTypes.number,
        Name: PropTypes.string,
        name: PropTypes.string,
        isOccupiedPhysical: PropTypes.bool,
        isOccupiedLogical: PropTypes.bool,
        isReachable: PropTypes.bool,
    })
}

export default AreaOverview


