import React, { PropTypes } from 'react'
import { Link } from 'react-router'

const DeviceOverview = ({ oid, data }) => {
    const { model, eui, softwareVersion, lastMsgTime, lastMsgTimeString, status } = data
    const hexEui = eui
    const hexFw = softwareVersion
    let shortTime
    if (lastMsgTimeString) {
        shortTime = lastMsgTimeString
    } else {
        shortTime = lastMsgTime.slice(0,19)
    }
    let statusOutline
    let statusText

    switch(status) {
        case 0:
            statusOutline = "bg-etn-yellow"
            statusText = "black"
            break
        case 1:
            statusOutline = "bg-white grn-shadow"
            statusText = "black"
            break
        case 2:
            statusOutline = "bg-etn-red-light white"
            statusText = "white"
            break
        default:
            statusOutline = "bg-white"
            statusText = "black"
            break
    }

    return (
        <div className="center xs-col xs-col-6 sm-col-4 lg-col-3 px1 py1">
            <Link className={`${statusOutline} hover-box left-align py3 btn btn-primary`} to={`/wlm/objects/${oid}`}>
                <div className={`${statusText}`}>
                    <div className="h3 mb2">{model}</div>
                    <p>{hexEui}</p>
                    <p>fw: {hexFw}</p>
                    <p>msg: {shortTime}</p>
                </div>
            </Link>
        </div>
    )
}

DeviceOverview.propTypes = {
    oid: PropTypes.string.isRequired,
    data: PropTypes.shape({
        eui: PropTypes.string,
        model: PropTypes.string,
        lastMsgTimeString: PropTypes.string,
        lastMsgTime: PropTypes.string,
        status: PropTypes.number,
        softwareVersion: PropTypes.string
    })
}

export default DeviceOverview


