import React, { PropTypes } from 'react'
import { Link } from 'react-router'

const OccEpOverview = ({ oid, data }) => {
    const { endpointStatus, endpointClass, endpointType, endpointSubtype, Name, name, isOccupied } = data

    const shortName = name.slice(0,-2)
    const occStatus = (isOccupied === true ? "TRUE" : "FALSE" )
    const statusOutline = (isOccupied === true ? "grn-shadow" : "rd-shadow")
    const epTypeArray = [endpointClass, endpointType, endpointSubtype]

    let statusBg, statusText, epType

    switch(epTypeArray.join(' ')) {
        case '2 1 2':
            epType = "Eaton Occ Sensor"
            break
        default:
            epType = "type data missing"
            break
    }

    switch(endpointStatus) {
        case 0:
            statusBg = "bg-etn-yellow"
            statusText = "black"
            break
        case 1:
            statusBg = "bg-white"
            statusText = "black"
            break
        case 2:
            statusBg = "bg-etn-red-light"
            statusText = "white"
            break
        default:
            statusBg = "bg-white"
            statusText = "black"
            break
    }

    return (
        <div className="center xs-col xs-col-6 md-col-4 lg-col-3 px1 py1">
            <Link className={`${statusOutline} ${statusBg} hover-box left-align py3 btn btn-primary`} to={`/wlm/objects/${oid}`}>
                <div className={`${statusText}`}>
                    <div className="h3 mb1">{shortName}</div>
                    <p className="mb2">{Name}</p>
                    <p>Type: {epType}</p>
                    <p>Occupied: <b>{occStatus}</b></p>
                </div>
            </Link>
        </div>
    )
}

OccEpOverview.propTypes = {
    oid: PropTypes.string.isRequired,
    data: PropTypes.shape({
        endpointClass: PropTypes.number,
        endpointType: PropTypes.number,
        endpointSubtype: PropTypes.number,
        endpointStatus: PropTypes.number,
        id: PropTypes.number,
        Name: PropTypes.string,
        name: PropTypes.string,
        isOccupied: PropTypes.bool,
    })
}

export default OccEpOverview


