import axios from 'axios'
import pickBy from 'lodash/pickBy'
import findKey from 'lodash/findKey'
import { CALL_CAPI, Schemas } from '../middleware/capi'
import { CALL_DIAGNOSTIC } from '../middleware/diagnostic'
import { browserHistory } from 'react-router'

let BASE_URL = ''
let DIAGNOSTIC_URL = ''

if (process.env.NODE_ENV === 'production') {
    BASE_URL = window.location.origin + '/v1/'
} else {
    // BASE_URL = window.location.origin + '/v1/'
    BASE_URL = 'https://192.168.1.100/v1/'
}


export const CAPI_LOGIN_REQUEST = 'CAPI_LOGIN_REQUEST'
export const CAPI_LOGIN_SUCCESS = 'CAPI_LOGIN_SUCCESS'
export const CAPI_LOGIN_FAILURE = 'CAPI_LOGIN_FAILURE'

const capiRequestLogin = (creds) => ({
    type: CAPI_LOGIN_REQUEST,
    isFetching: true,
    isAuthenticated: false,
    creds
})

const capiReceiveLogin = (user) => ({
    type: CAPI_LOGIN_SUCCESS,
    isFetching: false,
    isAuthenticated: true,
    capi_token: user.capi_token,
    userType: user
})

const capiLoginError = (message) => ({
    type: CAPI_LOGIN_FAILURE,
    isFetching: false,
    isAuthenticated: false,
    message
})

export const CAPI_LOGOUT_REQUEST = 'CAPI_LOGOUT_REQUEST'
export const CAPI_LOGOUT_SUCCESS = 'CAPI_LOGOUT_SUCCESS'

const capiRequestLogout = () => ({
    type: CAPI_LOGOUT_REQUEST,
    isFetching: true,
    isAuthenticated: true
})

const capiReceiveLogout = () => ({
    type: CAPI_LOGOUT_SUCCESS,
    isFetching: false,
    isAuthenticated: false
})

export const capiLoginUser = (creds) => {
    return dispatch => {
        dispatch(capiRequestLogin(creds))

        return axios.post(BASE_URL + 'authentication/login', {
            'userName':`${creds.username}`,'password':`${creds.password}`
        }).then((response) => {
            sessionStorage.setItem('capi_token', response.data.token)
            sessionStorage.setItem('capi_user', response.data.userType)
            const userType = response.data.userType
            dispatch(capiReceiveLogin(userType))
            if (userType === 0) {
                browserHistory.push('/wlm/ver')
            } else {
                browserHistory.push('/wlm/system')
            }
        }).catch((error) => {
            if (error.response) {
                dispatch(capiLoginError(error.response.data.description))
            } else {
                dispatch(capiLoginError('Gateway Error'))
            }
            console.log(BASE_URL)
            console.log(error)
        })
    }
}

// Logs the user out
export const capiLogoutUser = () => (dispatch) => {
    dispatch(capiRequestLogout())
    sessionStorage.removeItem('capi_token')
    sessionStorage.removeItem('capi_user')
    browserHistory.push('/wlm/')
    dispatch(capiReceiveLogout())
}

// OID list request actions
export const CAPI_OID_CLEAR_ERROR = 'CAPI_OID_CLEAR_ERROR'
export const CAPI_OIDS_REQUEST = 'CAPI_OIDS_REQUEST'
export const CAPI_OIDS_SUCCESS = 'CAPI_OIDS_SUCCESS'
export const CAPI_OIDS_FAILURE = 'CAPI_OIDS_FAILURE'
export const CAPI_OIDS_FILTER_REQUEST = 'CAPI_OIDS_FILTER_REQUEST'
export const CAPI_OIDS_FILTER_SUCCESS = 'CAPI_OIDS_FILTER_SUCCESS'

const capiClearOidError = () => ({
    type: CAPI_OID_CLEAR_ERROR
  })

export const handleClearError = () => (dispatch) => {
    return dispatch(capiClearOidError())
}

const fetchOids = () => ({
    [CALL_CAPI]: {
        endpoint: 'oidlist',
        types: [CAPI_OIDS_REQUEST, CAPI_OIDS_SUCCESS, CAPI_OIDS_FAILURE],
        request: 'GET',
        schema: Schemas.OID_LIST,
        body: null
    }
})

export const loadOids = () => (dispatch) => {
    return dispatch(fetchOids())
}

const beginFilterOids = () => ({
    type: CAPI_OIDS_FILTER_REQUEST
})

const handleFilterOids = (filteredList) => ({
    type: CAPI_OIDS_FILTER_SUCCESS,
    filteredList
})

export const filterOids = (term) => {
    return (dispatch, getState) => {
        dispatch(beginFilterOids())
        const fullList = getState().oids.oidRef
        const isValue = (value) => {
            const lowerCaseValue = value.toLowerCase()
            const lowerCaseTerm = term.toLowerCase()
            if (lowerCaseValue.includes(lowerCaseTerm)) {
                return true
            } else {
                return false
            }
        }
        if (term === '') {
            dispatch(handleFilterOids(fullList))
        } else {
            const filteredList = pickBy(fullList,isValue)
            dispatch(handleFilterOids(filteredList))
        }
    }
}

