// Device actions
export const DEVICES_LIST_REQUEST = 'DEVICES_LIST_REQUEST'
export const DEVICES_GET_NODE_MGR = 'DEVICES_GET_NODE_MGR'
export const DEVICES_LIST_SUCCESS = 'DEVICES_LIST_SUCCESS'
export const DEVICES_LIST_FAILURE = 'DEVICES_LIST_FAILURE'
export const DEVICES_FILTER_REQUEST = 'DEVICES_FILTER_REQUEST'
export const DEVICES_FILTER_SUCCESS = 'DEVICES_FILTER_SUCCESS'

const foundNodeMgr = (nodeMgrOid) => ({
    type: DEVICES_GET_NODE_MGR,
    nodeMgr: nodeMgrOid
})

const fetchDevices = oid => ({
    [CALL_CAPI]: {
        endpoint: `oid/${oid}`,
        types: [DEVICES_LIST_REQUEST, DEVICES_LIST_SUCCESS, DEVICES_LIST_FAILURE],
        request: 'GET',
        schema: Schemas.OID,
        body: null
    }
})

export const loadDevices = () => {
    return (dispatch, getState) => {
        let nodeMgrOid
        if (Object.keys(getState().oids.oidRef).length === 0 || !getState().devices.nodeMgr) {
            dispatch(loadOids()
            ).then(() => {
                return getState().oids.oidRef
            }).then(oidRef => {
                if (!getState().devices.nodeMgr) {
                    nodeMgrOid = findKey(oidRef, (item) => (item === 'ZigBeeNodeManager'))
                    dispatch(foundNodeMgr(nodeMgrOid))                
                }
            }).then(() => {
                nodeMgrOid = getState().devices.nodeMgr
                dispatch(fetchDevices(nodeMgrOid))
            })
        } else {
            nodeMgrOid = getState().devices.nodeMgr
            dispatch(fetchDevices(nodeMgrOid))
        }
    }
}

const beginFilterDevices = () => ({
    type: DEVICES_FILTER_REQUEST
})

const handleFilterDevices = (filteredList) => ({
    type: DEVICES_FILTER_SUCCESS,
    filteredList
})

export const filterDevices = (term) => {
    return (dispatch, getState) => {
        dispatch(beginFilterDevices())
        const fullList = getState().devices.deviceRef
        const isValue = (obj) => {
            for (let key in obj) {
                if({}.hasOwnProperty.call(obj,key)) {
                    if (term.startsWith('0x') === true) {
                        let hexValue = '0x' + obj[key].toString(16)
                        if (hexValue.includes(term)) {
                            return true
                        } else {
                            continue
                        }
                    } else {
                        let value = obj[key].toString()
                        let lowerCaseValue = value.toLowerCase()
                        let lowerCaseTerm = term.toLowerCase()
                        if (lowerCaseValue.includes(lowerCaseTerm)) {
                            return true
                        } else {
                            continue
                        }
                    }
                }
                return false
            }
        }
        if (term === '') {
            dispatch(handleFilterDevices(fullList))
        } else {
            const filteredList = pickBy(fullList,isValue)
            dispatch(handleFilterDevices(filteredList))
        }
    }
}

