// Diagnostic actions
export const DIAG_API_REQUEST = 'DIAG_API_REQUEST'
export const DIAG_API_SUCCESS = 'DIAG_API_SUCCESS'
export const DIAG_API_FAILURE = 'DIAG_API_FAILURE'
export const DIAG_UPLOAD_START = 'DIAG_UPLOAD_START'
export const DIAG_UPLOAD_PROGRESS = 'DIAG_UPLOAD_PROGRESS'
export const DIAG_UPLOAD_COMPLETE = 'DIAG_UPLOAD_COMPLETE'
export const DIAG_UPLOAD_FAILURE = 'DIAG_UPLOAD_FAILURE'

const fileUploadBegin = () => ({
    type: DIAG_UPLOAD_START,
    isUploading: true
})

const fileUploadProgress = (progress) => ({
    type: DIAG_UPLOAD_PROGRESS,
    uploadProgress: progress
})

const fileUploadComplete = () => ({
    type: DIAG_UPLOAD_COMPLETE,
    isUploading: false
})

const getGatewayUpdate = () => ({
    [CALL_DIAGNOSTIC]: {
        endpoint: 'update/gateway',
        types: [DIAG_API_REQUEST, DIAG_API_SUCCESS, DIAG_API_FAILURE],
        request: 'GET',
        body: null
    }
})

const postGatewayUpdate = () => ({
    [CALL_DIAGNOSTIC]: {
        endpoint: 'update/gateway',
        types: [DIAG_API_REQUEST, DIAG_API_SUCCESS, DIAG_API_FAILURE],
        request: 'POST',
        body: true
    }
})

const postGatewayTime = data => ({
    [CALL_DIAGNOSTIC]: {
        endpoint: 'system/time',
        types: [DIAG_API_REQUEST, DIAG_API_SUCCESS, DIAG_API_FAILURE],
        request: 'POST',
        body: data
    }
})

const fetchGatewayTime = () => ({
    [CALL_DIAGNOSTIC]: {
        endpoint: 'system/time',
        types: [DIAG_API_REQUEST, DIAG_API_SUCCESS, DIAG_API_FAILURE],
        request: 'GET',
        body: null
    }
})

export const loadGatewayTime = () => (dispatch) => {
    return dispatch(fetchGatewayTime())
}

export const setGatewayTime = (data) => (dispatch) => {
    return dispatch(postGatewayTime(data))
}

export const loadGatewayUpdate = () => (dispatch) => {
    return dispatch(getGatewayUpdate())
}

export const startGatewayUpdate = () => (dispatch) => {
    return dispatch(postGatewayUpdate())
}

export const fileUpload = (files) => {
    return dispatch => {
        console.log('uploading ' + files[0].name + ' to: ' + DIAGNOSTIC_URL)
        dispatch(fileUploadBegin())

        let data = new FormData()
        files.forEach(function(file) {
            data.append('file', file)
        }, this);
        
        return axios.post(DIAGNOSTIC_URL + 'update/file', data, {
            onUploadProgress: function(progressEvent) {
                let percentCompleted = Math.round((progressEvent.loaded * 100) / progressEvent.total)
                dispatch(fileUploadProgress(percentCompleted))
            }
        }).then((response) => {
            dispatch(fileUploadComplete())
            dispatch(loadGatewayUpdate())
            // dispatch(getOtaUpdate())
        }).catch((error) => {
            if (error.response) {
                dispatch(capiLoginError(error.response.data.description))
            } else {
                dispatch(capiLoginError('Upload Error'))
            }
            console.log(error.config)
        })
    }
}


