import * as ActionTypes from '../actions'

// system reducer
function system(state = {
        isFetching: false,
        netMgr: null,
        engController: null,
        hasNet: false,
        hasEng: false,
        lightEng: null,
        lightEngData: {},
        netMgrData: {},
        engData: {}
    }, action) {
    switch (action.type) {
        case ActionTypes.SYSTEM_NET_REQUEST:
            return Object.assign({}, state, {
                isFetching: true,
            })
        case ActionTypes.SYSTEM_GET_NET_MGR:
            return Object.assign({}, state, {
                isFetching: false,
                netMgr: action.netMgr,
            })
        case ActionTypes.SYSTEM_NET_SUCCESS:
            return Object.assign({}, state, {
                isFetching: false,
                hasNet: true,
                netMgrData: action.response.entities.object,
            })
        case ActionTypes.SYSTEM_NET_FAILURE:
            return Object.assign({}, state, {
                hasNet: false,
                isFetching: false,
            })
        case ActionTypes.SYSTEM_ENG_REQUEST:
            return Object.assign({}, state, {
                isFetching: true,
            })
        case ActionTypes.SYSTEM_GET_ENG:
            return Object.assign({}, state, {
                isFetching: false,
                engController: action.engController,
            })
        case ActionTypes.SYSTEM_ENG_SUCCESS:
            return Object.assign({}, state, {
                isFetching: false,
                hasEng: true,
                engData: action.response.entities.object,
            })
        case ActionTypes.SYSTEM_ENG_FAILURE:
            return Object.assign({}, state, {
                hasEng: false,
                isFetching: false,
            })
        default:
            return state
    }
}

export default system


