import React, { Component, PropTypes } from 'react'
import { connect } from 'react-redux'
import ReactModal from 'react-modal'

import { loadOidDetails, performOidPatch, handleClearError } from '../actions'
import ActionButton from '../components/ActionButton'
import ListItem from '../components/ListItem'
import ListLinkItem from '../components/ListLinkItem'

const loadData = ({ oid, loadOidDetails }) => {
  loadOidDetails(oid)
}

const performAction = ({ oid, performOidPatch }, data) => {
  performOidPatch(oid, JSON.stringify(data))
}

const clearError = ({handleClearError}) => {
  handleClearError()
}

class OidDetail extends Component {
  static propTypes = {
      oid: PropTypes.string.isRequired,
      entities: PropTypes.object,
      isFetching: PropTypes.bool.isRequired,
      loadOidDetails: PropTypes.func.isRequired,
      handleClearError: PropTypes.func.isRequired,
      performOidPatch: PropTypes.func
  }

  componentWillMount() {
    loadData(this.props)
  }

  componentWillReceiveProps(nextProps) {
    if (nextProps.oid !== this.props.oid) {
      loadData(nextProps)
    }
  }

  handleActionClose = () => {
    console.log('closing...')
    clearError(this.props)
  }

  onLoadDetailsClick = () => {
    loadData(this.props)
  }

  onActionClick = (data) => {
    performAction(this.props, data)
  }

  objToArray(obj) {
    let objTable = []
    let linkedObjTable = []
    let linkedArrayTable = []
    let actionTable = []

    for (let thisKey in obj) {
      if ({}.hasOwnProperty.call(obj,thisKey)) {
        let currentObject = obj[thisKey]

        // This needs a lot of cleanup. Works for now...
        if (thisKey === "object") {
          let mainObject  = currentObject[this.props.oid]
          for (let objKey in mainObject) {
            if ({}.hasOwnProperty.call(mainObject,objKey)) {
              let item = {}
              if (objKey === "Name") {
                objTable.unshift(<h1 className="h3 ml3 mb2" key={objKey}>{mainObject[objKey]}</h1>)
              } else if (objKey === "nodeObjectId") {
                item = {
                  name: objKey,
                  oid: mainObject[objKey]
                }
                linkedObjTable.push(<ListLinkItem item={item} key={objKey} />)
              } else if (Array.isArray(mainObject[objKey]) === true) {
                continue
              } else if (objKey.startsWith('~') === true) {
                actionTable.push(<ActionButton name={objKey} onActionClick={this.onActionClick} isPatching={this.props.isPatching} key={objKey}/>)
              } else {
                item = {
                  key: objKey,
                  value: String(mainObject[objKey])
                }
                objTable.push(<ListItem item={item} key={objKey} />)
              }
            }
          }
        } else if (thisKey === "linkedObjects") {
          for (let objKey in currentObject) {
            if ({}.hasOwnProperty.call(currentObject,objKey)) {
              let item = {}
              if (currentObject[objKey].hasOwnProperty('oid')) {
                item = {
                  name: currentObject[objKey].name || currentObject[objKey].Name,
                  oid: currentObject[objKey].oid
                }
                linkedObjTable.push(<ListLinkItem item={item} key={objKey} />)
              }
            }
          }
        } else {
          console.log(thisKey)
          linkedArrayTable.push(<h2 className="h3 mt4 ml3 mb3 clearfix" key={thisKey}>{thisKey}</h2>)
          for (let objKey in currentObject) {
            if ({}.hasOwnProperty.call(currentObject,objKey)) {
              let item = {}
              if ({}.hasOwnProperty.call(currentObject[objKey],'model')) {
                item = {
                  name: currentObject[objKey].model,
                  oid: currentObject[objKey].oid
                }
              } else if ({}.hasOwnProperty.call(currentObject[objKey],'name')) {
                item = {
                  name: currentObject[objKey].name,
                  oid: currentObject[objKey].oid
                }
              } else {
                console.log('an object with no name! ' + currentObject[objKey].Name)
                item = {
                  name: currentObject[objKey].Name,
                  oid: currentObject[objKey].oid
                }
              }
              linkedArrayTable.push(<ListLinkItem item={item} key={objKey} />)
            }
          }
        }
      }
    }

    let renderedObjectData = (
      <div>
        <div>{objTable}</div>
        {linkedObjTable.length > 0 &&
          <div>
            <h2 className="h3 mt4 ml3 mb3" key={'linkedObj'}>Linked Objects</h2>
            <div>{linkedObjTable}</div>
          </div>
        }
        {actionTable.length > 0 &&
          <div className="clearfix">
            <h2 className="h3 mt4 ml3 mb3" key={'linkedObj'}>Actions</h2>
            <div className="clearfix">
              <div>{actionTable}</div>
            </div>
          </div>
        }
        {linkedArrayTable.length > 0 &&
          <div className="clearfix">
            {linkedArrayTable}
          </div>
        }
      </div>
    )

    return renderedObjectData
  }

  render() {
    const { entities, isFetching, isPatching, errorMessage } = this.props
    const hasErrorMessage = (Object.keys(errorMessage).length === 0 ? false : true)

    let oidArray = this.objToArray(entities)

    return (
      <div className="p2 mx-auto max-width-3">
        <div className="center mb3">
          <button onClick={this.onLoadDetailsClick} className="btn btn-primary">
            Refresh
          </button>
        </div>
        {!isFetching &&
          <div>{oidArray}</div>  
        }
        {isFetching &&
          <h2 className="center">Loading...</h2>
        }
        <ReactModal 
            isOpen={isPatching || hasErrorMessage}
            contentLabel="onRequestClose send"
            className="modal mx-auto max-width-1 mt4 py3 px4"
            onRequestClose={this.handleActionClose}
            overlayClassName="overlay">
            {isPatching && 
             <div>Sending action...</div> 
            }
            {hasErrorMessage &&
              <div>
                <div className="h3">Error: {errorMessage.errorCode}</div>
                <div className="py2">{errorMessage.description}</div>
              </div>
            }
        </ReactModal>
      </div>
    )
  }
}

const mapStateToProps = (state, ownProps) => {
  const oid = ownProps.params.oid

  const { oids } = state
  const { currentOid, isFetching, isPatching, errorMessage } = oids
  const {
    entities
  } = currentOid

  return {
    entities,
    isFetching,
    isPatching,
    errorMessage,
    oid,
  }
}

export default connect(mapStateToProps, {
  loadOidDetails,
  performOidPatch,
  handleClearError
})(OidDetail)



