import React from 'react'
import { Route } from 'react-router'
import App from './containers/App'
import DeviceList from './containers/DeviceList'
import LightEngine from './containers/LightEngine/LightEngine'
import Area from './containers/LightEngine/Area'
import OccSet from './containers/LightEngine/OccSet'
import Zone from './containers/LightEngine/Zone'
import OidList from './containers/OidList'
import OidDetail from './containers/OidDetail'
// import Settings from './containers/Settings'
import System from './containers/System'
import Update from './containers/Update'
import Version from './containers/zVersion'

const Routes = (
  <Route path="/wlm" component={App}>
    <Route path="objects" component={OidList} />
    <Route path="objects/:oid" component={OidDetail} />
    <Route path="devices" component={DeviceList} />
    <Route path="system" component={System} />
    <Route path="system/update" component={Update} />
    <Route path="engine" component={LightEngine} />
    <Route path="engine/area/:oid" component={Area} />
    <Route path="engine/occ/:oid" component={OccSet} />
    <Route path="engine/zone/:oid" component={Zone} />
    <Route path="ver" component={Version} />
  </Route>
)

export default Routes


