import React, { PropTypes } from 'react'
import { Link } from 'react-router'

const OidListItem = ({ object }) => {
    const { oid, name } = object
    return (
        <div className="striped--near-white striped--table-link clearfix">
            <Link className="text-decoration-none" to={`/wlm/objects/${oid}`}>
                <div className="px2 py1 black">
                    {oid}: {name}
                </div>
            </Link>
        </div>
    )
}

OidListItem.propTypes = {
    object: PropTypes.object.isRequired
}

export default OidListItem


