import React, { Component, PropTypes } from 'react'
import ReactModal from 'react-modal'

export default class ActionButton extends Component {
    constructor() {
        super()
        this.state= {
            showModal: false
        }

        this.handleOpenModal = this.handleOpenModal.bind(this)
        this.handleCloseModal = this.handleCloseModal.bind(this)

    }

    dataInput(actionName) {
        if (actionName === '~fadeToLevel') {
            return (
                <div>
                    <input
                    className="input mt3"
                    ref="targetLevel"
                    placeholder="targetLevel"/>
                    <input
                    className="input mt3"
                    ref="transitionTime"
                    placeholder="transitionTime"/>
                </div>
            )
        }
        else {
            return (
                <input
                className="input mt3"
                ref="input"
                placeholder="Data... (blank = 1)"/>
            )
        }
    }

    handleOpenModal() {
        this.setState({
            showModal: true,
        })
    }

    handleCloseModal() {
        this.setState({
            showModal: false,
        })
    }

    handleClick(e) {
        e.preventDefault()
        let data = {}
        const actionKey = this.props.name
        if (this.props.name === '~fadeToLevel') {
            data['targetLevel'] = (this.refs.targetLevel.value === '' ? 100 : this.refs.targetLevel.value)
            data['transitionTime'] = (this.refs.transitionTime.value === '' ? 100 : this.refs.transitionTime.value)
            data[actionKey] = 1
        } else {
            let actionValue
            if (this.refs.input.value === '') {
                actionValue = 1
            } else {
                actionValue = this.refs.input.value
            }
            data[actionKey] = actionValue
        }
        this.props.onActionClick(data)
        this.handleCloseModal()
    }

    render() {
        const { name, style } = this.props
        const actionName = name.substr(1)
        const dataInputFields = this.dataInput(name)
        let btnStyle

        switch(style) {
            case 'outline':
                btnStyle = "btn-outline"
                break
            case 'outline white':
                btnStyle = "btn-outline white"
                break
            default:
                btnStyle = "btn-primary"
                break
        }

        return (
            <div>
                <div className="max-width-1 mb2 mr2 left">
                    <div onClick={this.handleOpenModal} className={`${btnStyle} btn py2 center`}>
                        {actionName}
                    </div>
                </div>
                <ReactModal 
                    isOpen={this.state.showModal}
                    contentLabel="onRequestClose Example"
                    onRequestClose={this.handleCloseModal}
                    className="modal mx-auto max-width-1 mt4 py3 px4"
                    overlayClassName="overlay"
                >
                    <h1>Action: {actionName}</h1>
                        <form onSubmit={(e) => this.handleClick(e)}>
                            {dataInputFields}
                            <button type="submit" className="right mt4 btn btn-primary">Send {actionName}</button>    
                        </form>
                </ReactModal>
            </div>
        )
    }
}

ActionButton.propTypes = {
    name: PropTypes.string,
    onActionClick: PropTypes.func,
    style: PropTypes.string
}


