import * as ActionTypes from '../actions'

// devices reducer
function devices(state = {
        isFetching: false,
        nodeMgr: null,
        hasDevices: false,
        deviceRef: {},
        deviceList: {},
        currentDevice: {}
    }, action) {
    switch (action.type) {
        case ActionTypes.DEVICES_LIST_REQUEST:
            return Object.assign({}, state, {
                isFetching: true,
            })
        case ActionTypes.DEVICES_GET_NODE_MGR:
            return Object.assign({}, state, {
                isFetching: false,
                nodeMgr: action.nodeMgr,
            })
        case ActionTypes.DEVICES_LIST_SUCCESS:
            return Object.assign({}, state, {
                isFetching: false,
                hasDevices: true,
                deviceRef: action.response.entities.nodeList,
                deviceList: action.response.entities.nodeList
            })
        case ActionTypes.DEVICES_LIST_FAILURE:
            return Object.assign({}, state, {
                hasDevices: false,
                isFetching: false,
            })
        case ActionTypes.DEVICES_FILTER_REQUEST:
            return Object.assign({}, state, {
            })
        case ActionTypes.DEVICES_FILTER_SUCCESS:
            return Object.assign({}, state, {
                deviceList: action.filteredList
            })
        default:
            return state
    }
}

export default devices


