import merge from 'lodash/merge'
import { combineReducers } from 'redux'
import * as ActionTypes from '../actions'

// Updates an entity cache in response to any action with response.entities.
const data = (state = { result: null, entities: {} }, action) => {
    if (action.response) {
        if (action.type === ActionTypes.LIGHTENGINE_SUCCESS) {
            return merge({}, state, action.response)
        } else if (action.type === ActionTypes.LIGHTENGINE_ENTITY_SUCCESS) {
            return merge({}, state, action.response)
        }
    }

    return state
}

// Updates error message to notify about the failed fetches.
const errorMessage = (state = null, action) => {
  const { type, error } = action

  if (type === ActionTypes.LIGHTENGINE_FAILURE) {
    return null
  } else if (error) {
    return error
  }

  return state
}

// lightEngine reducer
function info(state = {
        isFetching: false,
        lightEng: null
    }, action) {
    switch (action.type) {
        case ActionTypes.LIGHTENGINE_FIND:
            return Object.assign({}, state, {
                isFetching: true,
                lightEng: action.lightEng,
            })
        case ActionTypes.LIGHTENGINE_REQUEST:
            return Object.assign({}, state, {
                isFetching: true,
            })
        case ActionTypes.LIGHTENGINE_SUCCESS:
            return Object.assign({}, state, {
                isFetching: false,
            })
        case ActionTypes.LIGHTENGINE_ENTITY_SUCCESS:
            return Object.assign({}, state, {
                isFetching: false,
            })
        case ActionTypes.LIGHTENGINE_FAILURE:
            return Object.assign({}, state, {
                isFetching: false,
            })
        default:
            return state
    }
}

const lightEngine = combineReducers({
    data,
    info,
    errorMessage
})

export default lightEngine


