import merge from 'lodash/merge'
import { combineReducers } from 'redux'
import * as ActionTypes from '../actions'

// Updates an entity cache in response to any action with response.entities.
const system = (state = {}, action) => {
    if (action.response) {
        if (action.type === ActionTypes.DIAG_API_SUCCESS) {
            return merge({}, state, action.response.data)
        }
    }

    return state
}

// Updates error message to notify about the failed fetches.
const errorMessage = (state = null, action) => {
  const { type, error } = action

  if (type === ActionTypes.LIGHTENGINE_FAILURE) {
    return null
  } else if (error) {
    return error
  }

  return state
}

// update reducer
function update(state = {
    isFetching: false,
    isUploading: false,
    data: {
        gateway: {
            wacUpdateAvailable: false
        }
    },
    deviceUpdateAvailable: false,
    uploadProgress: 0
}, action) {
    switch (action.type) {
        case ActionTypes.DIAG_API_REQUEST:
            return Object.assign({}, state, {
                isFetching: true,
            })
        case ActionTypes.DIAG_API_SUCCESS:
            return Object.assign({}, state, {
                isFetching: false,
                // data: action.response.data
            })
        case ActionTypes.DIAG_API_FAILURE:
            return Object.assign({}, state, {
                isFetching: false,
            })
        case ActionTypes.DIAG_UPLOAD_START:
            return Object.assign({}, state, {
                isUploading: true,
                uploadProgress: 0,
            })
        case ActionTypes.DIAG_UPLOAD_PROGRESS:
            return Object.assign({}, state, {
                uploadProgress: action.uploadProgress,
            })
        case ActionTypes.DIAG_UPLOAD_COMPLETE:
            return Object.assign({}, state, {
                isUploading: false,
                uploadProgress: 0,
            })
        case ActionTypes.DIAG_UPLOAD_FAILURE:
            return Object.assign({}, state, {
                isUploading: false,
            })
        default:
            return state
    }
}

const linux = combineReducers({
    system,
    update,
    errorMessage
})

export default linux


