import React, { Component, PropTypes } from 'react'
import { connect } from 'react-redux'

import Navbar from '../components/Navbar'
import Login from '../components/Login'
import { capiLoginUser, capiLogoutUser } from '../actions'

class App extends Component {
  static propTypes = {
    isAuthenticated: PropTypes.bool.isRequired,
    errorMessage: PropTypes.string
  }

  handleLoginClick = (creds) => {
    this.props.capiLoginUser(creds)
  }

  render() {
    const { isAuthenticated, isFetching, errorMessage, userType, children } = this.props

    const allowedUser = (userType > 0 ? true : false)

    return (
      <div className="App">
        {isAuthenticated &&
          <div>
            <div>
            {allowedUser &&
              <Navbar
                isAuthenticated={isAuthenticated}
                isFetching={isFetching}
                onLogoutClick={this.props.capiLogoutUser}
              />
            }
            </div>
            {children}
          </div>
        }

        {!isAuthenticated &&
          <Login
            isFetching={isFetching}
            errorMessage={errorMessage}
            onLoginClick={this.handleLoginClick}
          />
        }
      </div>
    );
  }
}

const mapStateToProps = state => {
  const { auth } = state
  const { isAuthenticated, isFetching, errorMessage, userType } = auth
  return {
    isAuthenticated,
    isFetching,
    errorMessage,
    userType
  }
}

export default connect(mapStateToProps, {
  capiLoginUser,
  capiLogoutUser
})(App)



