import React, { Component, PropTypes } from 'react'
import { connect } from 'react-redux'
import { performOidPatch, loadEntity } from '../../actions'
import OccEpOverview from '../../components/overviews/OccEpOverview'
import OccSetOverview from '../../components/overviews/OccSetOverview'
import ActionButton from '../../components/ActionButton'
import LinkItem from '../../components/LinkItem'

const loadData = ({ oid, loadEntity }) => {
  loadEntity(oid)
}

const performAction = ({ oid, performOidPatch }, data) => {
  performOidPatch(oid, JSON.stringify(data))
}

class OccSet extends Component {
  static propTypes = {
    entities: PropTypes.object.isRequired,
    isFetching: PropTypes.bool,
    isPatching: PropTypes.bool,
    errorMessage: PropTypes.any,
    oid: PropTypes.string.isRequired,
    loadEntity: PropTypes.func.isRequired
  }

  componentWillReceiveProps(nextProps) {
    if (nextProps.oid !== this.props.oid) {
      loadData(nextProps)
    }
  }

  componentDidMount() {
    loadData(this.props)
  }

  onLoadDetailsClick = () => {
    loadData(this.props)
  }

  onActionClick = (data) => {
    performAction(this.props, data)
  }

  inputArray(obj) {
    let newArray = []
    for (let thisKey in obj) {
      if({}.hasOwnProperty.call(obj,thisKey)) {
        newArray.push(<OccEpOverview oid={thisKey} data={obj[thisKey]} key={thisKey}/>)
      }
    }
    return newArray
  }

  occSetArray(obj) {
    let newArray = []
    for (let thisKey in obj) {
      if({}.hasOwnProperty.call(obj,thisKey)) {
        newArray.push(<OccSetOverview oid={thisKey} data={obj[thisKey]} key={thisKey}/>)
      }
    }
    return newArray
  }

  occActions(obj) {
    let newArray = []
    const occActionKeys = ['occupiedAction', 'unoccupiedAction', 'returnToLevelAction']
    for (let thisKey in obj) {
      if({}.hasOwnProperty.call(obj,thisKey)) {
        if(occActionKeys.indexOf(thisKey) > -1) {
          newArray.push(<LinkItem name={thisKey} oid={obj[thisKey]} style={`outline white`} key={thisKey} />)
        }
      }
    }
    return newArray
  }

  getActions(obj) {
    let newArray = []
    for (let thisKey in obj) {
      if({}.hasOwnProperty.call(obj,thisKey)) {
        if (thisKey.startsWith('~') === true) {
          newArray.push(<ActionButton name={thisKey}
                          onActionClick={this.onActionClick}
                          isPatching={this.props.isPatching}
                          style={`outline white`}
                          key={thisKey}/>)
        }
      }
    }
    return newArray
  }

  render() {
    const { isFetching, oid, entities, result } = this.props

    let occSet, name, objectType, area, holdTime,
      occPhys, occLog, occSetInputArray,
      occSetActionLinks, manualActionArray, occSetSetArray


    if (entities && entities.objects) {
      occSet = entities.objects[oid]
      name = occSet.name
      objectType = occSet.Name
      holdTime = occSet.holdTime / (1000 * 60)
      occPhys = (occSet.isOccupiedPhysical === true ? "TRUE" : "FALSE")
      occLog = (occSet.isOccupiedLogical === true ? "TRUE" : "FALSE")
    }

    if (result && entities && entities.objects[oid].inputEndpoints) {
      const inputOids = entities.objects[oid].inputEndpoints
      const occSetOids = entities.objects[oid].occupancySets
      let inputObjects = {}
      let occSetObjects = {}

      area = entities.objects[oid].area.name

      for (let i = 0; i < inputOids.length; i++) {
        inputObjects[inputOids[i]] = entities.objects[inputOids[i]]
      }

      for (let i = 0; i < occSetOids.length; i++) {
        occSetObjects[occSetOids[i]] = entities.objects[occSetOids[i]]
      }

      occSetInputArray = this.inputArray(inputObjects)
      occSetSetArray = this.occSetArray(occSetObjects)

      occSetInputArray.push(occSetSetArray)
      occSetActionLinks = this.occActions(occSet)
      manualActionArray = this.getActions(occSet)
    }

    const buttonClass = (isFetching === true ? "progress" : "btn btn-outline")

    return (
      <div className="mb2">
        <div className="py3 px3 clearfix bg-etn-blue">
          <div className="max-width-3 mx-auto">
            <div className="mt3 mb1 h4 white">{objectType}</div>
            <div className="mb2 h2 white">{name}</div>
            <hr />
            <div className="white py1">
              <div className="left">
                <div className="mb1">Area: {area}</div>
                <div className="mb1">Hold Time: {holdTime} min.</div>
                <div className="mb1">Occupied Physical: {occPhys}</div>
                <div className="mb1">Occupied Logical: {occLog}</div>
              </div>
              <div onClick={this.onLoadDetailsClick} className={`${buttonClass} right`}>
                {isFetching ? '' : 'Refresh'}
              </div>
            </div>
          </div>
        </div>
        <div className="clearfix py2 bg-etn-dark-purple">
          <div className="max-width-4 mx-auto px2">
            <div className="sm-col sm-col-6 clearfix">
              <div className="h3 white mb2">Occupancy Actions</div>
              {occSetActionLinks}
            </div>
            <div className="sm-col-right sm-col-6 clearfix">
              <div className="h3 white mb2">Manual Actions</div>
              {manualActionArray}
            </div>
          </div>
        </div>
        {occSetInputArray &&
          <div className="clearfix py3 bg-lt-gray">
            <div className="max-width-4 mx-auto">
              <div className="h2 mb2 ml1">Input Endpoints</div>
              <div className="mxn2">
                {occSetInputArray}
              </div>
            </div>
          </div>
        }
      </div>
    )
  }
}

const mapStateToProps = (state, ownProps) => {
    const oid = ownProps.params.oid

    const { lightEngine, oids } = state
    const { isPatching } = oids
    const { data, info } = lightEngine
    const { isFetching } = info
    const { entities, result } = data

    return {
        entities,
        result,
        isFetching,
        isPatching,
        oid
    }
}

export default connect(mapStateToProps, {
  performOidPatch,
  loadEntity,
})(OccSet)



