import React, { Component, PropTypes } from 'react'
import { connect } from 'react-redux'
import { loadLightEng } from '../../actions'
import AreaOverview from '../../components/overviews/AreaOverview'

const loadData = ({ loadLightEng }) => {
  loadLightEng()
}

class LightEngine extends Component {
  static propTypes = {
      entities: PropTypes.object.isRequired,
      result: PropTypes.number,
      isFetching: PropTypes.bool.isRequired,
      loadLightEng: PropTypes.func.isRequired
  }

  componentDidMount() {
    loadData(this.props)
  }

  areaArray(obj) {
    let newArray = []
    for (let thisKey in obj) {
      if({}.hasOwnProperty.call(obj,thisKey)) {
        newArray.push(<AreaOverview oid={thisKey} data={obj[thisKey]} key={thisKey}/>)
      }
    }
    return newArray
  }

  render() {
    const { entities, result, lightEng, isFetching, loadLightEng } = this.props

    const buttonClass = (isFetching === true ? "progress" : "btn btn-outline")

    let showAreas

    if (lightEng && result && entities.objects[lightEng]) {
      const areaOids = entities.objects[lightEng].areas
      let areaObjects= {}

      for (let i = 0; i < areaOids.length; i++) {
        areaObjects[areaOids[i]] = entities.objects[areaOids[i]]
      }

      showAreas = this.areaArray(areaObjects)
    }

    return (
      <div className="mb2">
        <div className="py3 px3 clearfix bg-etn-blue">
          <div className="max-width-3 mx-auto">
            <div className="mt3 mb2 h2 white">Light Engine</div>
            <hr />
            <div className="white py1">
              <div className="left">

              </div>
              <div onClick={loadLightEng} className={`${buttonClass} mt1 right`}>
                {isFetching ? '' : 'Refresh'}
              </div>
            </div>
          </div>
        </div>
        {showAreas &&
          <div className="clearfix py3 bg-lt-gray">
            <div className="max-width-4 mx-auto">
              <div className="h2 mb2 ml1">Areas</div>
              <div className="mxn2">
                {showAreas}
              </div>
            </div>
          </div>
        }
      </div>
    )
  }
}

const mapStateToProps = state => {
    const { lightEngine } = state
    const { info, data } = lightEngine
    const { isFetching, lightEng } = info
    const { entities, result } = data
    return {
        entities,
        result,
        lightEng,
        isFetching
    }
}

export default connect(mapStateToProps, {
  loadLightEng,
})(LightEngine)



