import React, { PropTypes } from 'react'
import { Link } from 'react-router'

const ZoneOverview = ({ oid, data }) => {
    const { zoneStatus, actualLevel, id, Name, name, isReachable } = data

    const reachableStatus = (isReachable === true ? "TRUE" : "FALSE" )
    // const statusOutline = (isOccupiedLogical === true ? "grn-shadow" : "rd-shadow")

    let statusBg, statusText

    switch(zoneStatus) {
        case 'GOOD':
            statusBg = "bg-white"
            statusText = "black"
            break
        default:
            statusBg = "bg-etn-red-light"
            statusText = "white"
            break
    }

    return (
        <div className="center xs-col xs-col-6 md-col-4 lg-col-3 px1 py1">
            <Link className={`${statusBg} overview-box hover-box left-align py3 btn btn-primary`} to={`/wlm/engine/zone/${oid}`}>
                <div className={`${statusText}`}>
                    <div className="h3 mb1">{name}</div>
                    <p className="mb2">{Name}</p>
                    <p>ID: {id}</p>
                    <p>Actual Level: <b>{actualLevel}</b></p>
                    <p>Reachable: <b>{reachableStatus}</b></p>
                </div>
            </Link>
        </div>
    )
}

ZoneOverview.propTypes = {
    oid: PropTypes.string.isRequired,
    data: PropTypes.shape({
        actualLevel: PropTypes.number,
        zoneStatus: PropTypes.string,
        id: PropTypes.number,
        Name: PropTypes.string,
        name: PropTypes.string,
        isReachable: PropTypes.bool,
    })
}

export default ZoneOverview


