import React, { PropTypes } from 'react'
import { Link } from 'react-router'

const OccEpOverview = ({ oid, data }) => {
    const { Name, name, isOccupiedPhysical, isOccupiedLogical } = data

    const shortName = name //.slice(0,-2)
    const occPhyStatus = (isOccupiedPhysical === true ? "TRUE" : "FALSE" )
    const occLogStatus = (isOccupiedLogical === true ? "TRUE" : "FALSE" )
    const statusOutline = (isOccupiedLogical === true ? "grn-shadow" : "rd-shadow")
    // const epTypeArray = [endpointClass, endpointType, endpointSubtype]

    let statusBg = 'bg-white'
    let statusText = 'black'

    // switch(endpointStatus) {
    //     case 0:
    //         statusBg = "bg-etn-yellow"
    //         statusText = "black"
    //         break
    //     case 1:
    //         statusBg = "bg-white"
    //         statusText = "black"
    //         break
    //     case 2:
    //         statusBg = "bg-etn-red-light"
    //         statusText = "white"
    //         break
    //     default:
    //         statusBg = "bg-white"
    //         statusText = "black"
    //         break
    // }

    return (
        <div className="center xs-col xs-col-6 md-col-4 lg-col-3 px1 py1">
            <Link className={`${statusOutline} ${statusBg} hover-box left-align py3 btn btn-primary`} to={`/wlm/engine/occ/${oid}`}>
                <div className={`${statusText}`}>
                    <div className="h3 mb1">{shortName}</div>
                    <p className="mb2">{Name}</p>
                    <p>Occupied Physical: <b>{occPhyStatus}</b></p>
                    <p>Occupied Logical: <b>{occLogStatus}</b></p>
                </div>
            </Link>
        </div>
    )
}

OccEpOverview.propTypes = {
    oid: PropTypes.string.isRequired,
    data: PropTypes.shape({
        Name: PropTypes.string,
        name: PropTypes.string,
        isOccupiedPhysical: PropTypes.bool,
        isOccupiedLogical: PropTypes.bool,
    })
}

export default OccEpOverview


