import React, { PropTypes } from 'react'
import { Link } from 'react-router'
import Logout from './Logout'

const Navbar = ({ onLogoutClick }) => (

  <div>
    <nav className="main-nav bg-dark-blue clearfix px1">
      <div className="left py1">
        <Link className="main-nav__home px1 mr1 h5 white text-decoration-none" to="/wlm/">WaveLinx</Link>
        <Link activeClassName="active" className="main-nav__link py1 px1 h5 white text-decoration-none" to="/wlm/system">System</Link>
        <Link activeClassName="active" className="main-nav__link py1 px1 h5 white text-decoration-none" to="/wlm/devices">Devices</Link>
        <Link activeClassName="active" className="main-nav__link py1 px1 h5 white text-decoration-none" to="/wlm/engine">Engine</Link>
        <Link activeClassName="active" className="main-nav__link py1 px1 h5 white text-decoration-none" to="/wlm/objects">Objects</Link>
      </div>
      <div className="right mt1">
          <Logout onLogoutClick={onLogoutClick} />
      </div>
    </nav>
  </div>
)

Navbar.propTypes = {
  onLogoutClick: PropTypes.func.isRequired,
}

export default Navbar


