import React, { Component, PropTypes } from 'react'
import { connect } from 'react-redux'
import { loadNetMgr, loadEng, performOidPatch } from '../actions'
import ActionButton from '../components/ActionButton'

const loadData = ({ loadNetMgr, loadEng }) => {
  loadNetMgr()
  loadEng()
}

class System extends Component {
  static propTypes = {
    isFetching: PropTypes.bool.isRequired,
    netMgr: PropTypes.string,
    netMgrData: PropTypes.object.isRequired,
    engController: PropTypes.string,
    engData: PropTypes.object.isRequired
  }

  componentDidMount() {
    loadData(this.props)
  }

  handleReload = () => {
    return loadData(this.props)
  }

  onNetActionClick = (data) => {
    this.props.performOidPatch(this.props.netMgr, JSON.stringify(data))
  }

  handleSearch = (term) => {
    
  }

  render() {
    const { isFetching, netMgr, netMgrData, engController, engData } = this.props
    const buttonClass = (isFetching === true ? "progress" : "btn btn-outline")

    let nwStatus, fwVer, permitStatus, netMgrPermit, netMgrKickAll

    if (netMgrData[netMgr]) {
      nwStatus = netMgrData[netMgr].networkStatus
      permitStatus = netMgrData[netMgr].permitAssociation

      if({}.hasOwnProperty.call(netMgrData[netMgr],'~setPermitAssociation')) {
        netMgrPermit = <ActionButton name={'~setPermitAssociation'} onActionClick={this.onNetActionClick} key={'~setPermitAssociation'}/>
      }
      if({}.hasOwnProperty.call(netMgrData[netMgr],'~kickAllDevices')) {
        netMgrKickAll = <ActionButton name={'~kickAllDevices'} onActionClick={this.onNetActionClick} key={'~kickAllDevices'}/>
      }
    }

    if (engData[engController]) {
      fwVer = engData[engController].firmwareString
    }

    return (
      <div className="mb2">
        <div className="py3 px3 clearfix bg-etn-blue">
          <div className="max-width-3 mx-auto">
            <div className="mt3 mb2 h2 white">WaveLinx System</div>
            <hr className="" />
            <div className="mb2">
              <div onClick={this.handleReload} className={`${buttonClass} mt1 white right`}>
                {isFetching ? '' : 'Refresh'}
              </div>
            </div>
            <div className="white py1">
              <div className="left">
                <div className="mb1">FW ver: {fwVer}</div>
                <div className="mb1">Net Status: {nwStatus}</div>
                <div className="mb1">Permit Join: {permitStatus}</div>
              </div>
            </div>
          </div>
        </div>
        <div className="clearfix">
          <div className="max-width-3 mx-auto px2">
            <h2 className="h3 mt3 mb3">Network Actions</h2>
            <div className="">
              {netMgrPermit}
              {netMgrKickAll}
            </div>
          </div>
        </div>
      </div>
    )
  }
}

const mapStateToProps = state => {
  const { system } = state
  const { isFetching, netMgr, netMgrData, engController, engData } = system
  return {
    isFetching,
    netMgr,
    netMgrData,
    engController,
    engData
  }
}

export default connect(mapStateToProps, {
  loadNetMgr,
  loadEng,
  performOidPatch
})(System)



