import React, { Component } from 'react'
import debounce from 'lodash/debounce'

export default class SearchBar extends Component {
    constructor(props){
        super(props)
        this.state = {
            value: props.value
    }
    this.changed = debounce(this.props.changed, 250)
  }
  // pass in the Redux action dispatcher and the returned value via props
  static propTypes = {
    changed: React.PropTypes.func.isRequired
  }

  handleChange = e => {
    // React event weirdness requires storing the synthetic event
    const val = e.target.value
    this.setState({ value: val }, () => {
      this.changed(val)
    })
  }
  render() {
    return (
      <input
        className="input"
        ref="input"
        onChange={this.handleChange}
        placeholder="Search..."/>
    )
  }
}


