import * as ActionTypes from '../actions'

// oids reducer
function oids(state = {
        isFetching: false,
        isPatching: false,
        hasOids: false,
        oidRef: {},
        oidList: {},
        currentOid: {},
        actionResponse: {},
        errorMessage: {}
    }, action) {
    switch (action.type) {
        case ActionTypes.CAPI_OIDS_REQUEST:
            return Object.assign({}, state, {
                isFetching: true,
                errorMessage: {}
            })
        case ActionTypes.CAPI_OIDS_SUCCESS:
            return Object.assign({}, state, {
                isFetching: false,
                hasOids: true,
                oidRef: action.response.entities.objects.undefined,
                oidList: action.response.entities.objects.undefined,
            })
        case ActionTypes.CAPI_OIDS_FAILURE:
            return Object.assign({}, state, {
                isFetching: false,
                errorMessage: action.error
            })
        case ActionTypes.CAPI_OIDS_FILTER_REQUEST:
            return Object.assign({}, state, {
            })
        case ActionTypes.CAPI_OIDS_FILTER_SUCCESS:
            return Object.assign({}, state, {
                oidList: action.filteredList
            })
        case ActionTypes.CAPI_OID_DETAIL_REQUEST:
            return Object.assign({}, state, {
                isFetching: true,
                errorMessage: {}
            })
        case ActionTypes.CAPI_OID_DETAIL_SUCCESS:
            return Object.assign({}, state, {
                isFetching: false,
                currentOid: action.response,
            })
        case ActionTypes.CAPI_OID_DETAIL_FAILURE:
            return Object.assign({}, state, {
                isFetching: false,
                errorMessage: action.error
            })
        case ActionTypes.CAPI_OID_PATCH_REQUEST:
            return Object.assign({}, state, {
                isPatching: true,
                errorMessage: {}
            })
        case ActionTypes.CAPI_OID_PATCH_SUCCESS:
            return Object.assign({}, state, {
                isPatching: false,
                actionResponse: action.response,
            })
        case ActionTypes.CAPI_OID_PATCH_FAILURE:
            return Object.assign({}, state, {
                isPatching: false,
                errorMessage: action.error
            })
        case ActionTypes.CAPI_OID_CLEAR_ERROR:
            return Object.assign({}, state, {
                errorMessage: {},
                isPatching: false
            })
        default:
            return state
    }
}

export default oids


