import React, { Component, PropTypes } from 'react'

export default class Login extends Component {

  render() {
    const { errorMessage, isFetching } = this.props

    return (
      <div className="fixed center bg-etn-gradient pv4 top-0 left-0 right-0 bottom-0">
        <h1 className="white mt4 mb2 h1">WaveLinx Manager</h1>

        {errorMessage &&
          <p className="h3 my2 py2 white bg-etn-red bold">{errorMessage}</p>
        }

        {!isFetching &&
          <form className="mx-auto max-width-1 py2 px2" onSubmit={(event) => this.handleClick(event)}>
            <input type='text' ref='username' className="input mx-auto mb1" placeholder='Username'/>
            <input type='password' ref='password' className="input mx-auto mb3" placeholder='Password'/>
            <button type="submit" className="btn btn-outline white h3 py1 px3">
              Login
            </button>
          </form>
        }

        {isFetching &&
          <h2 className="white mt4">Logging in...</h2>
        }
      </div>
    )
  }

  handleClick(event) {
    event.preventDefault()
    const username = this.refs.username
    const password = this.refs.password
    const creds = { username: username.value.trim(), password: password.value.trim() }
    this.props.onLoginClick(creds)
  }
}

Login.propTypes = {
    isFetching: PropTypes.bool.isRequired,
    onLoginClick: PropTypes.func.isRequired,
    errorMessage: PropTypes.string,
    creds: PropTypes.string
}


