import axios from 'axios'

let DIAGNOSTIC_URL = '' 

if (process.env.NODE_ENV === 'production') {
    DIAGNOSTIC_URL = window.location.origin + '/config/v1/'
} else {
    DIAGNOSTIC_URL = 'https://192.168.1.101:8443/v1/'
}

// callCapi, inputs are:
//   endpoint - the CAPI URL endpoint
//   request - request type (GET, PATCH, etc.)
//   body - data to be sent with a PATCH, POST, etc. (nothing for GET)
const callDiagnostic = (endpoint, request, body) => {

    const fullUrl = (endpoint.indexOf(DIAGNOSTIC_URL) === -1) ? DIAGNOSTIC_URL + endpoint : endpoint
    // const token = sessionStorage.getItem('capi_token') || null
    const config = {
        method: request,
        url: fullUrl,
        headers: {
        //     'token': `${token}`,
            'Content-Type': 'application/json'
        },
        data: body
    }
    return axios(config)
        .then(response => {
            return response
        })
        .catch(error => {
            if (error.response) {
                return Promise.reject(error.response.data)
            } else {
                return Promise.reject(error)
            }
        })
}

// Action key that carries API call info interpreted by this Redux middleware.
export const CALL_DIAGNOSTIC = Symbol('Call DIAGNOSTIC')

// A Redux middleware that interprets actions with CALL_API info specified.
// Performs the call and promises when such actions are dispatched.
export default store => next => action => {

  const callDIAGNOSTIC = action[CALL_DIAGNOSTIC]

  if (typeof callDIAGNOSTIC === 'undefined') {
    return next(action)
  }

  let { endpoint } = callDIAGNOSTIC
  const { request, body, types } = callDIAGNOSTIC

  const actionWith = data => {
    const finalAction = Object.assign({}, action, data)
    delete finalAction[CALL_DIAGNOSTIC]
    return finalAction
  }

  const [ requestType, successType, errorType ] = types
  next(actionWith({ type: requestType }))

  return callDiagnostic(endpoint, request, body).then(
    response => next(actionWith({
        response,
        type: successType
      })),
    error => next(actionWith({
      error: error,
      type: errorType
    }))
  )
}



