import React, { Component, PropTypes } from 'react'
import { connect } from 'react-redux'
import { loadDevices, filterDevices } from '../actions'
import SearchBar from '../components/SearchBar'
import DeviceOverview from '../components/overviews/DeviceOverview'

const loadData = ({ loadDevices }) => {
  loadDevices()
}

class DeviceList extends Component {
  static propTypes = {
    deviceList: PropTypes.object.isRequired
  }

  componentDidMount() {
    loadData(this.props)
  }

  componentWillUnmount() {
    this.props.filterDevices('')
  }

  handleSearch = (term) => {
    this.props.filterDevices(term)
  }

  objToArray(obj) {
    let newArray = []
    for (let thisKey in obj) {
      if({}.hasOwnProperty.call(obj,thisKey)) {
        newArray.push(<DeviceOverview oid={thisKey} data={obj[thisKey]} key={thisKey}/>)
      }
    }
    return newArray
  }

  countGoodDevices(obj) {
    let good = 0
    for (let thisKey in obj) {
      if({}.hasOwnProperty.call(obj,thisKey)) {
        if (obj[thisKey].status === 1) {
          good += 1
        } else {
          continue
        }
      }
    }
    return good
  }

  render() {
    const { isFetching, deviceList, deviceRef, loadDevices } = this.props
    const deviceListArray = this.objToArray(deviceList)
    const goodDevices = this.countGoodDevices(deviceRef)
    const buttonClass = (isFetching === true ? "progress" : "btn btn-outline")

    let deviceNumber = 0
    let searchNumber = 0

    if (deviceRef) {
      deviceNumber = Object.keys(deviceRef).length
      searchNumber = Object.keys(deviceList).length
    }

    return (
      <div className="mb2">
        <div className="py3 px3 clearfix bg-etn-blue">
          <div className="max-width-3 mx-auto">
            <div className="mt3 mb3 h2 white left">Devices</div>
            <SearchBar changed={this.handleSearch} />
            <div className="white">
              <div className="left">
                <div className="mb1">Connected: {deviceNumber}</div>
                <div className="mb1">Matching: {searchNumber}</div>
                <div className="mb1">Good: {goodDevices}</div>
              </div>
              <div onClick={loadDevices} className={`${buttonClass} right`}>
                {isFetching ? '' : 'Refresh'}
              </div>
            </div>
          </div>
        </div>
        {deviceListArray &&
          <div className="clearfix py3 bg-lt-gray">
            <div className="max-width-4 mxn2 mx-auto">
              {deviceListArray}
            </div>
          </div>
        }
      </div>
    )
  }
}

const mapStateToProps = state => {
  const { devices } = state
  const { isFetching, deviceRef, deviceList } = devices
  return {
    isFetching,
    deviceRef,
    deviceList
  }
}

export default connect(mapStateToProps, {
  filterDevices,
  loadDevices
})(DeviceList)



