import { schema, normalize } from 'normalizr'
import axios from 'axios'

let BASE_URL = ''

if (process.env.NODE_ENV === 'production') {
    BASE_URL = window.location.origin + '/v1/'
} else {
    // BASE_URL = window.location.origin + '/v1/'
    BASE_URL = 'https://192.168.1.100/v1/'
}
// callCapi, inputs are:
//   endpoint - the CAPI URL endpoint
//   request - request type (GET, PATCH, etc.)
//   schema - type of response expected
//   body - data to be sent with a PATCH, POST, etc. (nothing for GET)
const callCapi = (endpoint, request, schema, body) => {

    const fullUrl = (endpoint.indexOf(BASE_URL) === -1) ? BASE_URL + endpoint : endpoint
    const token = sessionStorage.getItem('capi_token') || null
    const config = {
        method: request,
        url: fullUrl,
        headers: {
            'token': `${token}`,
            'Content-Type': 'application/json'
        },
        data: body
    }
    return axios(config)
        .then(response => {
                return Object.assign({},
                    normalize(response.data, schema)
                )
            })
            .catch(error => {
                if (error.response) {
                    return Promise.reject(error.response.data)
                } else {
                    return Promise.reject(error)
                }
            })
}

// individual schema definitions for all arrays objects
const actions = new schema.Entity('actions', {}, {idAttribute: 'oid'})
const areas = new schema.Entity('areas', {}, {idAttribute: 'oid'})
const daylightSets = new schema.Entity('daylightSets', {}, {idAttribute: 'oid'})
const devices = new schema.Entity('devices', {}, {idAttribute: 'oid'})
const endpointDecorators = new schema.Entity('endpointDecorators', {}, {idAttribute: 'oid'})
const endpoints = new schema.Entity('endpoints', {}, {idAttribute: 'oid'})
const endpointList = new schema.Entity('endpointList', {}, {idAttribute: 'oid'})
const inputEndpoints = new schema.Entity('inputEndpoints', {}, {idAttribute: 'oid'})
const occupancySets = new schema.Entity('occupancySets', {}, {idAttribute: 'oid'})
const networkInterfaces = new schema.Entity('networkInterfaces', {}, {idAttribute: 'oid'})
const nodeList = new schema.Entity('nodeList', {}, {idAttribute: 'oid'})
const OtaEndpoints = new schema.Entity('OtaEndpoints', {}, {idAttribute: 'oid'})
const outputEndpoints = new schema.Entity('outputEndpoints', {}, {idAttribute: 'oid'})
const sequenceObjects = new schema.Entity('sequenceObjects', {}, {idAttribute: 'oid'})
const scenes = new schema.Entity('scenes', {}, {idAttribute: 'oid'})
const schedules = new schema.Entity('schedules', {}, {idAttribute: 'oid'})
const zones = new schema.Entity('zones', {}, {idAttribute: 'oid'})

// schema definition for all linked objects (a single object returned at a given key)
const linkedObject = new schema.Entity('linkedObjects', {}, {idAttribute: 'oid'})

// here we go, folks... TODO - see if we can programmatically handle this mess
const object_old = new schema.Entity('object', {
    action: linkedObject,
    actions: [ actions ],
    actionSequence: linkedObject,
    area: linkedObject,
    areas: [ areas ],
    attachedToNetwork: linkedObject,
    clientNetworkProperties: linkedObject,
    closedLoopDaylightSensor: linkedObject,
    daylightSet: linkedObject,
    daylightSets: [ daylightSets ],
    demandResponseFader: linkedObject,
    dimmableLight: linkedObject,
    device: linkedObject,
    devices: [ devices ],
    endpointDecorators: [ endpointDecorators ],
    endpoints: [ endpoints ],
    endpointList: [ endpointList ],
    fader: linkedObject,
    inputEndpoint: linkedObject,
    inputEndpoints: [ inputEndpoints ],
    lightEngine: linkedObject,
    masterNetworkInterface: linkedObject,
    masterNetworkProperties: linkedObject,
    networkInterfaces: [ networkInterfaces ],
    networkManager: linkedObject,
    occupancySensor: linkedObject,
    occupancySet: linkedObject,
    occupancySets: [ occupancySets ],
    occupiedAction: linkedObject,
    outputEndpoints: [ outputEndpoints ],
    nodeList: [ nodeList ],
    OtaEndpoints: [ OtaEndpoints ],
    parentArea: linkedObject,
    powerMeasurement: linkedObject,
    returnToLevelAction: linkedObject,
    schedules: [ schedules ],
    selectScene: linkedObject,
    sequenceObjects: [ sequenceObjects ],
    scenes: [ scenes ],
    target: linkedObject,
    unoccupiedAction: linkedObject,
    'Wallstation LED': linkedObject,
    zigBeeNode: linkedObject,
    zone: linkedObject,
    zones: [ zones ]
}, {idAttribute: 'oid'})

const objectList = new schema.Entity('objects')

const object = new schema.Entity('objects', {}, {idAttribute: 'oid'})

const objects = new schema.Entity('objects', {
    areas: [ object ],
    daylightSets: [ object ],
    devices: [ object ],
    endpointDecorators: [ object ],
    endpoints: [ object ],
    endpointList: [ object ],
    inputEndpoint: object,
    inputEndpoints: [ object ],
    networks: [ object ],
    networkInterfaces: [ object ],
    occupiedAction: object,
    occupancySets: [ object ],
    outputEndpoints: [ object ],
    nodeList: [ object ],
    OtaEndpoints: [ object ],
    returnToLevelAction: object,
    schedules: [ object ],
    sequenceObjects: [ object ],
    unoccupiedAction: object,
    scenes: [ object ],
    zones: [ object ]
}, {idAttribute: 'oid'})

// only a couple of schemas so far...
// will certainly need more soon
export const Schemas = {
    OBJECT: objects,
    OID: object_old,
    OID_LIST: objectList
}


// Action key that carries API call info interpreted by this Redux middleware.
export const CALL_CAPI = Symbol('Call CAPI')

// A Redux middleware that interprets actions with CALL_API info specified.
// Performs the call and promises when such actions are dispatched.
export default store => next => action => {

  const callCAPI = action[CALL_CAPI]

  if (typeof callCAPI === 'undefined') {
    return next(action)
  }

  let { endpoint } = callCAPI
  const { request, schema, body, types } = callCAPI

  const actionWith = data => {
    const finalAction = Object.assign({}, action, data)
    delete finalAction[CALL_CAPI]
    return finalAction
  }

  const [ requestType, successType, errorType ] = types
  next(actionWith({ type: requestType }))

  return callCapi(endpoint, request, schema, body).then(
    response => next(actionWith({
        response,
        type: successType
      })),
    error => next(actionWith({
      error: error,
      type: errorType
    }))
  )
}



