import React, { Component, PropTypes } from 'react'
import { connect } from 'react-redux'
import { fileUpload } from '../actions'

const uploadFile = ({ fileUpload }, files) => {
  fileUpload(files)
}

class Update extends Component {
  static propTypes = {
    isUploading: PropTypes.bool.isRequired,
    wacUpdateAvailable: PropTypes.bool.isRequired,
    deviceUpdateAvailable: PropTypes.bool.isRequired,
    uploadProgress: PropTypes.number.isRequired,
  }

  handleUpload = (acceptedFiles, rejectedFiles) => {
    uploadFile(this.props, acceptedFiles)
    console.log('Accepted files: ', acceptedFiles)
    console.log('Rejected files: ', rejectedFiles)
  }

  onDrop(acceptedFiles, rejectedFiles) {
    this.handleUpload(acceptedFiles)
    console.log('Accepted files: ', acceptedFiles)
    console.log('Rejected files: ', rejectedFiles)
  }

  render() {
    const { isUploading, wacUpdateAvailable, deviceUpdateAvailable, uploadProgress } = this.props

    let isWacButtonDisabled, isDeviceButtonDisabled, wacButtonText, deviceButtonText

    isWacButtonDisabled = (wacUpdateAvailable === true ? false : true)
    isDeviceButtonDisabled = (deviceUpdateAvailable === true ? false : true)
    wacButtonText = (wacUpdateAvailable === true ? "WAC update available" : "No update available")
    deviceButtonText = (deviceUpdateAvailable === true ? "Device update available" : "No update available")

    const uploadPercentage = uploadProgress + '%'

    const progressBarStyle = {
      left: `${uploadPercentage}`,
      background: '#003D6E',
      position: 'absolute',
      top: 0,
      transition: 'all 0.1s ease-out',
    }

    return (
      <div className="mb2">
        <div className="py3 px3 clearfix bg-etn-blue">
          <div className="max-width-3 mx-auto">
              <div className="mt3 mb2 h2 white">
              <h2>System Update</h2>
            </div>
            <hr />
            <div className="white py1 mb3">
              <div className="left mb3">
                <button disabled={isWacButtonDisabled} title={wacButtonText} className="btn py2 px1 btn btn-outline">Update WAC</button>
                <button disabled={isDeviceButtonDisabled} title={deviceButtonText} className="btn py2 px1 ml1 btn btn-outline">Update Devices</button>
              </div>
            </div>
            {isUploading &&
              <div className="progress-bar-wrap progress-bar">
                <div className="progress-bar-bar progress-bar" style={progressBarStyle}></div>
              </div>
            }
          </div>
        </div>
        <div className="px2 py4 center mx-auto max-width-4">
        </div>
      </div>
    )
  }
}

const mapStateToProps = state => {
  const { linux } = state
  const { update } = linux
  const { isUploading, data, deviceUpdateAvailable, uploadProgress } = update
  const { gateway } = data
  const { wacUpdateAvailable } = gateway
  return {
    isUploading,
    wacUpdateAvailable,
    deviceUpdateAvailable,
    uploadProgress
  }
}

export default connect(mapStateToProps, {
  fileUpload
})(Update)



