import React, { PropTypes } from 'react'

const Logout = ({ onLogoutClick }) => (

    <div onClick={() => onLogoutClick()} className="btn btn-outline h5 py0 px1 white">
        Logout
    </div>
)

Logout.propTypes = {
  onLogoutClick: PropTypes.func.isRequired
}

export default Logout


