import React, { Component, PropTypes } from 'react'
import { connect } from 'react-redux'
import { loadOids, filterOids } from '../actions'
import OidListItem from '../components/OidListItem'
import SearchBar from '../components/SearchBar'

const loadData = ({ loadOids }) => {
  loadOids()
}

class OidList extends Component {
  static propTypes = {
    hasOids: PropTypes.bool.isRequired,
    loadOids: PropTypes.func.isRequired,
    filterOids: PropTypes.func.isRequired,
    oidList: PropTypes.object.isRequired,
    isFetching: PropTypes.bool.isRequired
  }

  componentWillMount() {
    loadData(this.props)
  }

  componentWillUnmount() {
    this.props.filterOids('')
  }

  handleSearch = (term) => {
    this.props.filterOids(term)
  }

  objToArray(obj) {
    let newArray = []
    for (let thisKey in obj) {
      if({}.hasOwnProperty.call(obj,thisKey)) {
        let item = {
          oid: thisKey,
          name: obj[thisKey]
        }
        newArray.push(<OidListItem object={item} key={thisKey} />)
      }
    }
    return newArray
  }

  render() {
    const { hasOids, oidRef, oidList, loadOids, isFetching } = this.props
    const oidListArray = this.objToArray(oidList)
    const buttonClass = (isFetching === true ? "progress" : "btn btn-outline")

    let objectNumber = 0
    let searchNumber = 0

    if (oidRef) {
      objectNumber = Object.keys(oidRef).length
      searchNumber = Object.keys(oidList).length
    }

    return (
      <div className="mb2">
        <div className="py3 px3 clearfix bg-etn-blue">
          <div className="max-width-3 mx-auto">
            <div className="mt3 mb3 h2 white left">Objects</div>
            <SearchBar changed={this.handleSearch} />
            <div className="white">
              <div className="left">
                <div className="mb1">Objects: {objectNumber}</div>
                <div className="mb1">Matching: {searchNumber}</div>
              </div>
              <div onClick={loadOids} className={`${buttonClass} right`}>
                {isFetching ? '' : 'Refresh'}
              </div>
            </div>
          </div>
        </div>
        {hasOids &&
          <div className="mx-auto max-width-3">
            <div>{oidListArray}</div>  
          </div>
        }
        {isFetching &&
          <h2 className="center mt4">Loading...</h2>
        }
      </div>
    )
  }
}

const mapStateToProps = state => {
  const { oids } = state
  const { hasOids, oidRef, oidList, isFetching } = oids
  return {
    hasOids,
    oidRef,
    oidList,
    isFetching
  }
}

export default connect(mapStateToProps, {
  loadOids,
  filterOids
})(OidList)



